.input {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: 100%;
  height: 48px;
  line-height: 1.5;
  outline: 0;
  padding: 0 0 0 12px;
  border-radius: 5px;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  color: rgba(40, 50, 80, 0.74);
  background-color: rgba(40, 50, 80, 0.05);
  border: 2px solid transparent;
  box-sizing: border-box;
}
