:global(.overflow-hidden) {
  overflow: hidden;
}

.login {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.3);
  text-align: center;
  padding-top: 150px;

  .login-form {
    width: 420px;
    max-width: 95vh;
    margin: 0 auto;
    background-color: white;
    padding: 3%;
    border-radius: 7px;
  }

  .switch-btn {
    padding: 1rem 0.5rem;
    margin-left: auto;
    display: block;
    cursor: pointer;
    width: fit-content;
  }

  .signup-btn {
    background: #ff6464;
  }

  input {
    margin-bottom: 2rem;
  }
}
