.button {
  display: block;
  width: 100%;
  height: 48px;
  color: #fff;
  background: #5586ff;
  font-size: 16px;
  font-weight: 400;
  border: 0;
  border-radius: 5px;
  margin: 0;
  padding: 0;
  outline: none;
  cursor: pointer;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: center;
}
